								'<span class="text-brand">Cards For Women</span> empowers women by providing them with a safe space to share their struggles, or as we call them, pain points - to then co-create solutions, or strategies for them. We provide a platform for women to anonymously express their vulnerable experiences, one card at a time.',
							cta: { text: "Reserve your deck", url: null },
