<template>
	<a-layout style="background: #fff;">
		<a-back-top
			:style="{
				height: '40px',
				width: '40px',
				lineHeight: '40px',
				borderRadius: '4px',
				backgroundColor: '#c25959',
				color: '#fff',
				textAlign: 'center',
				fontSize: '20px'
			}"
		>
			<div class="ant-back-top-inner">
				<a-icon type="arrow-up"></a-icon>
			</div>
		</a-back-top>
		<header-bar ref="mainHeader"></header-bar>
		<main ref="mainContent">
			<router-view></router-view>
		</main>
		<main-footer></main-footer>
		<div ref="mainFooter" class="copyright-footer">
			<span class="meta">
				<fa :icon="['fas', 'copyright']"></fa> 2020
				<a-tooltip title="It's us again!">Cards for Women, LLC. </a-tooltip>
			</span>
			<a-divider type="vertical"></a-divider>
			<span class="meta">
				<router-link :to="{ name: 'privacy-policy' }">Privacy Policy</router-link>
			</span>
			<a-divider type="vertical"></a-divider>
			<span class="meta">
				<router-link :to="{ name: 'terms-of-service' }">Terms of Service</router-link>
			</span>
		</div>
	</a-layout>
</template>

<script>
import { mapState, mapActions } from "vuex";
import HeaderBar from "./components/HeaderBar";
import MainFooter from "./components/MainFooter";

export default {
	components: {
		HeaderBar,
		MainFooter
	},
	computed: mapState(["appData", "cards", "startLoading"]),
	methods: mapActions(["updateAuthStatus"]),
	beforeMount() {
		if (window.localStorage.getItem("token")) {
			this.updateAuthStatus(true);
		}
	},
	mounted() {
		const headerHeight = this.$refs.mainHeader.$el.offsetHeight;
		const footerHeight = this.$refs.mainFooter.offsetHeight;
		const newHeight = window.innerHeight - footerHeight - headerHeight;

		this.$refs.mainContent.style.minHeight = newHeight + "px";
	}
};
</script>
