import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
	{
		name: "home",
		path: "/",
		component: () => import("../views/About")
	},
	{
		name: "coming-soon",
		path: "/coming-soon",
		component: () => import("../views/ComingSoon")
	},
	{
		name: "account",
		path: "/account",
		beforeEnter: (to, from, next) => {
			if (window.localStorage.getItem("token")) {
				next();
			} else {
				next({ name: "sign-in" });
			}
		},
		component: () => import("../views/Account")
	},
	{
		name: "sign-in",
		path: "/sign-in",
		beforeEnter: (to, from, next) => {
			if (window.localStorage.getItem("token")) {
				next({ name: "account" });
			} else {
				next();
			}
		},
		component: () => import("../views/SignIn")
	},
	// {
	// 	name: "reserve",
	// 	path: "/reserve",
	// 	component: () => import("../views/Reserve")
	// },
	{
		name: "cards",
		path: "/cards",
		component: () => import("../views/BoardHome")
	},
	{
		name: "team",
		path: "/team",
		component: () => import("../views/Team")
	},
	{
		name: "toolkits",
		path: "/toolkits",
		component: () => import("../views/TemplatesPage")
	},
	{
		name: "create-account",
		path: "/create-account",
		component: () => import("../views/CreateAccount")
	},
	{
		name: "jobs",
		path: "/jobs",
		component: () => import("../views/JobsBoard")
	},
	{
		name: "terms-of-service",
		path: "/terms-of-service",
		component: () => import("../views/TermsOfService.vue")
	},
	{
		name: "privacy-policy",
		path: "/privacy-policy",
		component: () => import("../views/PrivacyPolicy.vue")
	},
	{
		name: "not-found",
		path: "/*",
		component: () => import("../views/NotFound")
	},
	// off-page links
	{
		name: "instagram",
		path: "/instagram",
		beforeEnter() {
			location.href = "https://instagram.com/cardsforwomen";
		}
	},
	{
		name: "facebook",
		path: "/facebook",
		beforeEnter() {
			location.href = "https://facebook.com/cardsforwomen";
		}
	},
	{
		name: "linkedin",
		path: "/linkedin",
		beforeEnter() {
			location.href = "https://www.linkedin.com/company/cardsforwomen/";
		}
	},
	{
		name: "medium",
		path: "/medium",
		beforeEnter() {
			location.href = "https://medium.com/blogs-for-women";
		}
	},
	{
		name: "medium-form",
		path: "/medium-form",
		beforeEnter() {
			// form
			// location.href = "https://docs.google.com/forms/d/1OgSgC_RSYEOYf6r0J96CJ0bim6TKSAuyE1OXmBTJDXs";

			//	page
			location.href = "https://sites.google.com/cardsforwomen.org/articlesubmissions/home";
		}
	}
];

// TODO: be a member to post a painpoint
export default new VueRouter({
	mode: "history",
	routes,
	scrollBehavior(to, from, savedPosition) {
		if (savedPosition) {
			return savedPosition;
		} else {
			return { x: 0, y: 0 };
		}
	}
});
