<template>
	<section class="c4w-section">
		<div class="container">
			<a-row :gutter="[30, 30]" type="flex" justify="center">
				<a-col :xs="24" :lg="14">
					<h2>We're everywhere. Follow us!</h2>
					<p>{{ footerData.contact.data.description }}</p>
					<p>
						<a-row style="font-size: 1.25rem;" :gutter="10" align="middle" type="flex">
							<a-col>
								<router-link :to="{ name: 'facebook' }">
									<fa :icon="['fab', 'facebook-square']" />
								</router-link>
							</a-col>
							<a-col>
								<router-link :to="{ name: 'linkedin' }">
									<fa :icon="['fab', 'linkedin']" />
								</router-link>
							</a-col>
							<a-col>
								<router-link :to="{ name: 'medium' }">
									<fa :icon="['fab', 'medium']" />
								</router-link>
							</a-col>
							<a-col>
								<router-link :to="{ name: 'instagram' }">
									<fa :icon="['fab', 'instagram']" />
								</router-link>
							</a-col>
						</a-row>
					</p>
					<p :style="{ marginBottom: '3rem' }">
						<a-card :style="{ maxWidth: '30rem' }">
							<h3>Or just clap for us!</h3>
							<a-row type="flex" align="middle" :gutter="25">
								<a-col span="7" @click="() => clapIt()" class="clapper">
									<img src="images/icons/clap.svg" class="clap" />
									<img src="images/icons/clapped.svg" class="clapped" />
								</a-col>
								<a-col span="17">
									<div
										class="text-4xl font-light leading-none"
										:class="claps < 1 ? 'text-platinum' : 'text-brand'"
									>
										<a-statistic
											title="Total Claps: 23403"
											:value="claps"
											:style="{ marginRight: '50px' }"
										/>
									</div>
								</a-col>
							</a-row>
						</a-card>
					</p>
				</a-col>
				<a-col :xs="24" :lg="10">
					<a-form layout="vertical" :form="contactForm" @submit.prevent="submitForm">
						<h2>Let's get in touch.</h2>
						<a-row :gutter="[10, 20]">
							<a-col :xs="24" :lg="12">
								<a-form-item>
									<a-input
										allow-clear
										v-decorator="['firstName', { rules: rules.name }]"
										placeholder="Full Name"
									>
									</a-input>
								</a-form-item>
							</a-col>
							<a-col :xs="24" :lg="12">
								<a-form-item>
									<a-input
										allow-clear
										v-decorator="['lastName', { rules: rules.name }]"
										placeholder="Last Name"
									>
									</a-input>
								</a-form-item>
							</a-col>
							<a-col span="24">
								<a-form-item>
									<a-input
										allow-clear
										v-decorator="['email', { rules: rules.email }]"
										placeholder="Your Email"
									>
									</a-input>
								</a-form-item>
							</a-col>
							<a-col span="24">
								<a-form-item>
									<a-textarea
										allow-clear
										v-decorator="['message', { rules: rules.message }]"
										placeholder="What's up?"
										:rows="6"
									>
									</a-textarea>
								</a-form-item>
							</a-col>
							<a-col span="24">
								<a-form-item>
									<a-button
										:loading="isSendingMessage"
										type="primary"
										icon="message"
										html-type="submit"
									>
										Send it
									</a-button>
								</a-form-item>
							</a-col>
						</a-row>
					</a-form>
				</a-col>
			</a-row>
		</div>
	</section>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
	data() {
		return {
			contactForm: this.$form.createForm(this, { name: "contactForm" }),
			claps: 0,
			rules: {
				email: [
					{ required: true, message: "Email is required!", trigger: "blur" },
					{ type: "email", message: "This is not an email!", trigger: ["change", "blur"] }
				],
				name: [{ required: true, message: "Name is required.", trigger: "blur" }],
				message: [{ required: true, message: "What did you want to say?", trigger: "blur" }]
			},
			isSendingMessage: false,
			messageSent: false
		};
	},
	computed: {
		footerData() {
			return this.$store.state.appData.globalSections.footer;
		},
		...mapState(["appSettings", "showFullFooter"])
	},
	methods: {
		...mapActions(["generatePassword"]),
		submitForm() {
			this.contactForm.validateFields((err, values) => {
				if (!err) {
					this.isSendingMessage = true;
					// TODO: Fix contact message;
					this.$client
						.createItem("contact_form_messages", {
							user_email: values.email,
							user_first_name: values.firstName,
							user_last_name: values.firstName,
							user_message: values.message
						})
						.then(() => {
							this.$notification.success({
								message: "Thanks for contacting us!",
								description:
									"We'll get back to you as soon as possible, meanwhile checkout the rest of the site.",
								placement: "bottomRight"
							});
						})
						.catch((err) => {
							this.$notification.error({
								message: err.message ? err.message : "Message was not sent!",
								description:
									"Sorry about that! Try again or send us a message at contact@cardsforwomen.org!",
								placement: "bottomRight"
							});
						})
						.then(() => {
							this.contactForm.resetFields();
							this.isSendingMessage = false;
						});
				} else {
					setTimeout(() => {
						this.contactForm.resetFields();
					}, 4000);
				}
			});
		},
		clapIt() {
			this.claps++;
		}
	}
};
</script>
