<template>
	<div>
		<contact-block v-if="showBlock()"></contact-block>
	</div>
</template>

<script>
import ContactBlock from "../blocks/ContactBlock";

export default {
	components: {
		ContactBlock
	},
	methods: {
		showBlock() {
			let bool = false;
			switch (this.$route.name) {
				case "account":
				case "sign-in":
				case "create-account":
				case "templates":
					bool = false;
					break;
				default:
					bool = true;
					break;
			}

			return bool;
		}
	}
};
</script>
